.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	/* justify-content: center; */
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.result {
	margin: auto;
	display: flex;
	flex-wrap: wrap;
	width: 500px;
}

.result span {
	font-size: 100px;
	margin-left: 200px;
}

.result p {
	color: hotpink;
	text-align: center;
	margin-left: auto;
	margin-right: auto;
	display: flex;
	flex-wrap: wrap;
	width: 500px;
	margin-bottom: 0;
	margin-top: 0;
}

p#loser {
	color: #61dafb;
	width: 269px;
	margin-left: auto;
	margin-right: auto;
}

@media (max-width: 1024px) {
	p#loser {
		width: 230px;
	}
}

@media (max-width: 823px) {
	p#loser {
		width: 170px;
	}
}

@media (max-width: 568px) {
	p#loser {
		width: 155px;
	}
}

@media (max-width: 414px) {
	.result p {
		width: 400px;
	}
	.result {
		width: 400px;
	}
	.result span {
		margin-left: 160px;
		font-size: 80px;
	}
	p#loser {
		width: 166px;
	}
}

@media (max-width: 375px) {
	.result p {
		width: 330px;
	}
	.result {
		width: 350px;
	}
	.result span {
		margin-left: 130px;
	}
}

@media (max-width: 320px) {
	.result p {
		width: 264px;
	}
	.result {
		width: 264px;
	}
	.result span {
		margin-left: 95px;
	}
	p#loser {
		width: 155px;
	}
}
